// next-app/pages/user/[id].js
import { useEffect, useState } from 'react';
import { useRouter } from 'next/router';
import axios from '../../services/api'; // Your configured axios instance
import { useAuth } from '../../context/AuthContext';
import Head from 'next/head';

export default function UserProfile() {
  const router = useRouter();
  const { id } = router.query;
  const { user: currentUser, isAuthenticated, token, updateUser } = useAuth();
  const backendBaseUrl = process.env.NEXT_PUBLIC_BACKEND_URL || 'http://localhost:5000';

  const [profileUser, setProfileUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [isFollowing, setIsFollowing] = useState(false);

  useEffect(() => {
    if (!id) return;

    const fetchUserProfile = async () => {
      try {
        setLoading(true);
        setError(null);
        const res = await axios.get(`/users/${id}`);
        setProfileUser(res.data);

        if (currentUser && currentUser.following && res.data) {
          const followingStatus = currentUser.following.some(followedId => followedId === res.data._id);
          setIsFollowing(followingStatus);
        }
      } catch (err) {
        console.error('Failed to fetch user profile:', err);
        setError(err.response?.data?.error || 'Failed to load user profile.');
        setProfileUser(null);
      } finally {
        setLoading(false);
      }
    };

    fetchUserProfile();
  }, [id, currentUser]);

  const handleFollowToggle = async () => {
    if (!isAuthenticated || !currentUser || !profileUser) {
      alert('You must be logged in to follow users.');
      router.push('/login');
      return;
    }
    if (currentUser._id === profileUser._id) {
        alert('You cannot follow yourself!');
        return;
    }

    try {
      let res;
      if (isFollowing) {
        res = await axios.delete(`/users/${profileUser._id}/unfollow`, {
          headers: { Authorization: `Bearer ${token}` },
        });
      } else {
        res = await axios.post(`/users/${profileUser._id}/follow`, {}, {
          headers: { Authorization: `Bearer ${token}` },
        });
      }

      alert(res.data.message);
      setIsFollowing(!isFollowing);

      if (updateUser && currentUser) {
        const updatedFollowing = isFollowing
          ? currentUser.following.filter(followedId => followedId !== profileUser._id)
          : [...currentUser.following, profileUser._id];

        updateUser({ ...currentUser, following: updatedFollowing });

        setProfileUser(prev => ({
            ...prev,
            followersCount: isFollowing ? prev.followersCount - 1 : prev.followersCount + 1
        }));
      }

    } catch (err) {
      console.error('Follow/Unfollow failed:', err.response?.data || err.message);
      alert('Action failed: ' + (err.response?.data?.error || 'Unknown error'));
      setIsFollowing(!isFollowing);
    }
  };

  if (loading) return <p className="text-center mt-8">Loading profile...</p>;
  if (error) return <p className="text-center mt-8 text-red-500">{error}</p>;
  if (!profileUser) return <p className="text-center mt-8">User not found.</p>;

  return (
    <div className="max-w-3xl mx-auto p-6 bg-white shadow-md rounded-lg mt-8">
      <Head>
        <title>{profileUser.username}'s Profile - DevCommunity</title>
      </Head>
      <div className="flex flex-col items-center">
        <img
          src={`<span class="math-inline">\{backendBaseUrl\}</span>{profileUser.avatarUrl}`}
          alt={`${profileUser.username}'s Avatar`}
          className="w-32 h-32 rounded-full object-cover border-4 border-blue-400 mb-4"
        />
        <h1 className="text-3xl font-bold text-gray-800 mb-2">{profileUser.username}</h1>
        <p className="text-gray-600 mb-4">{profileUser.email}</p>

        {profileUser.bio && (
          <p className="text-gray-700 text-center max-w-prose mb-4 italic">"{profileUser.bio}"</p>
        )}

        <div className="flex space-x-6 mb-6">
          <div className="text-center">
            <span className="font-bold text-lg">{profileUser.followersCount}</span>
            <p className="text-gray-600">Followers</p>
          </div>
          <div className="text-center">
            <span className="font-bold text-lg">{profileUser.followingCount}</span>
            <p className="text-gray-600">Following</p>
          </div>
        </div>

        {isAuthenticated && currentUser && currentUser._id !== profileUser._id && (
          <button
            onClick={handleFollowToggle}
            className={`px-6 py-2 rounded-lg font-semibold transition-colors duration-200 ${
              isFollowing
                ? 'bg-red-500 text-white hover:bg-red-600'
                : 'bg-blue-500 text-white hover:bg-blue-600'
            }`}
          >
            {isFollowing ? 'Unfollow' : 'Follow'}
          </button>
        )}
      </div>

      <div className="mt-8 pt-6 border-t border-gray-200">
        <h3 className="text-2xl font-bold text-gray-800 mb-4 text-center">Posts by {profileUser.username}</h3>
        <p className="text-center text-gray-500">No posts found yet.</p>
      </div>
    </div>
  );
}